
















































































































































































































import Vue from "vue";

export default Vue.extend({
  data(): Record<string, string> {
    return {
      // Last updated datestamp: Update this whenever the policy changes
      // As if that's going to be often...
      lastUpdated: new Date(
        "Fri Sep 17 2021 19:36:42 GMT-0500"
      ).toLocaleDateString(undefined, {
        year: "numeric",
        month: "long",
        day: "2-digit",
      }),
      pageTitle: "Privacy Policy - Ryan Hodin",
      pageDescription:
        "An in-depth description of all the personal information I don't collect.",
    };
  },

  // Lifecycle - While we're mounted, request other components to save space below 500px width or 100vh height
  // (we have lots of content to display, and we want small screens to have room to display it)
  mounted() {
    document.body.classList.add(
      "space-saver-500px-wide",
      "space-saver-100vh-tall"
    );
  },
  beforeDestroy() {
    document.body.classList.remove(
      "space-saver-500px-wide",
      "space-saver-100vh-tall"
    );
  },
});
